import React, { Component } from "react";
import { BrowserRouter as Router, Switch, Route, HashRouter} from "react-router-dom";

import Web3 from "web3";
import YoutubeGameJson from "./abis/YoutubeGame.json";

import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap-icons/font/bootstrap-icons.css";

import Main from "./components/Main";

import "./css/main.css";
import Navbar from "./components/Navbar";
import RandomNumberHistory from "./components/RandomNumberHistory";
import Howtoplay from "./components/Howtoplay";

class App extends Component {

  async componentWillMount() {
    this.setState({ loading: false });

  }

  async ConnectAccount() {
    await this.loadWeb3();
    await this.loadBlockChatData();
  };

  async loadWeb3() {
    if (window.ethereum) {
      window.web3 = new Web3(window.ethereum);
      await window.ethereum.enable();

    } else if (window.web3) {
      window.web3 = new Web3(window.web3.currentProvider);
    } else {
      window.alert(
        "Non-Ethereum browser detected. You should consider trying MetaMask!"
      );
    }
  }

  async loadBlockChatData() {
    const web3 = window.web3;
    const accounts = await web3.eth.getAccounts();
    web3.eth.defaultAccount = accounts[0];
    this.setState({ account: accounts[0] });

    const networkId = await web3.eth.net.getId();

    if (networkId === 108 || networkId === 18) {
      this.setState({ connected: true });
      const networkAddress = '0xDafe71F6b8FdD7f36bAf0324981B2F90Dadf76b9git';
      const gameContract = await web3.eth.Contract(YoutubeGameJson.abi, networkAddress);
      this.setState({ gameContract });

      this.setState({ loading: false });
    } else {
      window.alert("Blockchat contract not deployed to detected network. Please switch to thundercore");
      this.switchToThundercoreTestNet();
    }
  }

  async switchToThundercoreTestNet(){
    try {
      await window.ethereum.request({
        method: "wallet_switchEthereumChain",
        params: [{ chainId: "0x12" }],
      });
      this.loadBlockChatData();
    } catch (error) {
      if (error.code === 4902) {
        try {
          await window.ethereum.request({
            method: "wallet_addEthereumChain",
            params: [
              {
                chainId: "0x12",
                chainName: "TestNet ThunderCore",
                rpcUrls: ["https://testnet-rpc.thundercore.com"],
                nativeCurrency: {
                  name: "ThunderCore",
                  symbol: "TT",
                  decimals: 18,
                },
                blockExplorerUrls: ["https://explorer-mumbai.maticvigil.com"],
              },
            ],
          });
          this.loadBlockChatData();

        } catch (error) {
          console.log(error.message);
        }
      }
    }
  }


  async switchToThundercoreMainnet(){
    try {
      await window.ethereum.request({
        method: "wallet_switchEthereumChain",
        params: [{ chainId: "0x6c" }],
      });
      this.loadBlockChatData();
    } catch (error) {
      if (error.code === 4902) {
        try {
          await window.ethereum.request({
            method: "wallet_addEthereumChain",
            params: [
              {
                chainId: "0x6c",
                chainName: "ThunderCore Mainnet",
                rpcUrls: ["https://mainnet-rpc.thundercore.com"],
                nativeCurrency: {
                  name: "ThunderCore",
                  symbol: "TT",
                  decimals: 18,
                },
                blockExplorerUrls: ["https://scan.thundercore.com/"],
              },
            ],
          });
          this.loadBlockChatData();

        } catch (error) {
          console.log(error.message);
        }
      }
    }
  }


  constructor(props) {
    super(props);
    this.state = {
      connected: false,
      account: "",
      gameStartDate: 0,
      gameContract: null,
      loading: true,
    };

    this.ConnectAccount = this.ConnectAccount.bind(this)
  }

  render() {
    return (!this.state.loading
      ?
      <HashRouter>
        <Navbar  account={this.state.account} ConnectAccount={this.ConnectAccount} />
        <Switch>
          <Route path="/" exact>
            <Main  account={this.state.account} connected={this.state.connected} ConnectAccount={this.ConnectAccount} gameContract={this.state.gameContract} />
          </Route >
          <Route path="/history" exact>
            <RandomNumberHistory gameContract={this.state.gameContract} connected={this.state.connected} />
          </Route>
          <Route path="/how-to-play" exact>
            <Howtoplay />
          </Route >
        </Switch >
      </HashRouter >
      :
      <div className="text-center">
        loading...
      </div>

    );
  }
}

export default App;
