import React, { useEffect, useState } from 'react'

import "../css/CustomTable.css"

export default function RandomNumberHistory(props) {
    const { gameContract, connected } = props;

    const [history30, setHistory30] = useState([]);
    const [loadingHistory, setLoadingHistory] = useState(true);

    useEffect(() => {
        if (connected) {
            loadWinnerHistory();
        }
    }, []);

    useEffect(() => {
        if (connected) {
            loadWinnerHistory();
        }
    }, [gameContract]);

    function timeConverter(UNIX_timestamp) {
        var a = new Date(UNIX_timestamp * 1000);
        var months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        var year = a.getFullYear();
        var month = months[a.getMonth()];
        var date = a.getDate();
        var hour = a.getHours();
        var min = a.getMinutes();
        var sec = a.getSeconds();
        var time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min + ':' + sec;
        return time;
    }


    const loadWinnerHistory = async () => {
        setLoadingHistory(true);

        const currentRound = await gameContract.methods.currentRound().call();
        let _latestGame = currentRound.toNumber() - 1;
        let _lastGame = 1;

        if (_latestGame > 30) {
            _lastGame = _latestGame - 30;
        } else {
            _lastGame = 1;
        }

        let _winnerArray = [];
        for (let i = _latestGame; i >= _lastGame; i--) {
            let oneItem = await gameContract.methods.minMaxHistoryInRound(i).call();
            let _historyItem = {
                'roundCount': i,
                "minValue": oneItem.MinValue.toString(),
                'maxValue': oneItem.MaxValue.toString(),
                'number': oneItem.Number.toString(),
                'time': oneItem.Date.toString()
            }
            _winnerArray.push(_historyItem);
        }

        setHistory30(_winnerArray);
        setLoadingHistory(false)

    }

    return (
        <div className="table-box mx-auto mt-2 p-4 text-light text-center">
            <h1>History</h1>
            <div className="table-bg py-4">
                <table class="table  text-light ">
                    <thead>
                        <tr>
                            <th>Round</th>
                            <th>Range</th>
                            <th>Random Number</th>
                            <th>Time</th>
                        </tr>
                    </thead>
                    <tbody>
                        {history30 && history30.map((history, key) => {

                            return (
                                <tr key={key}>
                                    <td>{history.roundCount}</td>
                                    <td>{history.minValue} - {history.maxValue}</td>
                                    <td>{history.number}</td>
                                    <td>{timeConverter(history.time)}</td>
                                </tr>
                            )
                        })
                        }

                    </tbody>

                </table>
                {loadingHistory &&
                    <div className="text-center">
                        <div class="spinner-border text-light my-1" role="status">
                        </div>
                        {!connected &&
                            < p > Please Connect for check history</p>
                        }

                    </div>
                }
            </div>

        </div >
    )
}
