import React, { useEffect, useRef, useState } from 'react'

import "../css/CustomButtons.css"
import "../css/MainBackground.css"

import bgImage from "../img/bg/heroheader-bg.png"
import banner from "../img/banner/banner.png"

export default function Main(props) {
    const { account, gameContract, connected, ConnectAccount } = props;

    const lowestNum = useRef(null);
    const highestNum = useRef(null);

    const [mainLoading, setMainLoading] = useState(true);
    const [currentRound, setCurrentRound] = useState(0);
    const [firstRoundMin, setFirstRoundMin] = useState(0);
    const [firstRoundMax, setFirstRoundMax] = useState(0);

    const [loadingRandomNumber, setLoadingRandomNumber] = useState(false);
    const [loadedRandomNumber, setLoadedRandomNumber] = useState(null);

    const [findingRandomNumberNotice, setFindingRandomNumberNotice] = useState(null);

    const [isAdmin, setisAdmin] = useState(false);


    useEffect(() => {
        if (connected) {
            loadData();
        }
    }, []);

    useEffect(() => {
        if (connected) {
            loadData();
        }
    }, [gameContract]);




    const connectToAccount = () => {
        if (!connected) {
            ConnectAccount()
        }
    }

    const loadData = async () => {
        setMainLoading(true);
        const getIsAdmin = await gameContract.methods.isAdmin().call();
        setisAdmin(getIsAdmin);
        if (!getIsAdmin) {
            setFindingRandomNumberNotice('Sorry! Only Admin can Generate Random Number')
        }
        const getCurrentRound = await gameContract.methods.currentRound().call();
        setCurrentRound(getCurrentRound.toNumber());
        console.log(getCurrentRound.toNumber());
        if (getCurrentRound.toNumber() === 1) {
            const _minFirst = await gameContract.methods.getFirstRoundMin().call();
            const _maxFirst = await gameContract.methods.getFirstRoundMax().call();
            setFirstRoundMin(_minFirst.toNumber());
            setFirstRoundMax(_maxFirst.toNumber());
        }
        setMainLoading(false);
    }


    const findRandomNumber = async () => {
        setFindingRandomNumberNotice(null);

        if (connected && gameContract != null) {

            var _lowestnum;
            var _highestnum;

            if (currentRound === 1) {
                _lowestnum = firstRoundMin;
                _highestnum = firstRoundMax;
            } else {
                _lowestnum = lowestNum.current.value;
                _highestnum = highestNum.current.value;

                if (1000 > _lowestnum || _lowestnum > 9999 || !_lowestnum) {
                    setFindingRandomNumberNotice('Lowest Number Must be greater than 999 and less than 10000');
                    return
                }
                if (1000 > _highestnum || _highestnum > 9999 || !_highestnum) {
                    setFindingRandomNumberNotice('Highest Number Must be greater than 999 and less than 10000');
                    return
                }

                if (_highestnum - _lowestnum <= 0) {
                    setFindingRandomNumberNotice('Highest number must be greater then lowest number');
                    return
                }
            }

            setLoadingRandomNumber(true);
            await gameContract.methods.findRandomNumberBetween(_lowestnum, _highestnum)
                .send({ from: account })
                .on('confirmation', (confirmationNumber, receipt) => {
                    loadRandomNumber();
                })
                .on('error', function (error, receipt) {
                    loadData();
                });
        } else {
            setMainLoading(false);
            connectToAccount();
        }
    }


    const loadRandomNumber = async () => {
        const getCurrentRound = await gameContract.methods.currentRound().call();
        setCurrentRound(getCurrentRound.toNumber());

        const _randomNumber = await gameContract.methods.minMaxHistoryInRound(currentRound).call();
        setLoadedRandomNumber({ 'lowestNum': _randomNumber.MinValue.toString(), 'highestNum': _randomNumber.MaxValue.toString(), 'randomNum': _randomNumber.Number.toString(), 'date': _randomNumber.Date.toString() });
        setLoadingRandomNumber(false)
    }

    function timeConverter(UNIX_timestamp){
        var a = new Date(UNIX_timestamp * 1000);
        var months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
        var year = a.getFullYear();
        var month = months[a.getMonth()];
        var date = a.getDate();
        var hour = a.getHours();
        var min = a.getMinutes();
        var sec = a.getSeconds();
        var time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min + ':' + sec ;
        return time;
      }


    return (
        <div className="main-box mx-auto mt-2 p-4 text-light text-center">
            <div className="bg-holder" style={{ backgroundImage: `url(${bgImage})`, backgroundPosition: 'center', backgroundSize: 'contain' }}></div>

            <div className="glow-div">
                <img className="img-fluid mb-4" src={banner} style={{ borderRadius: 20 }} alt="youtube banner" />
            </div>
            <div onClick={connectToAccount} className="glassBg-box mx-6 py-3">
                <h1>Find Random Number</h1>
            </div >
            {!connected
                ?
                <div className="my-4">
                    <h4>Not connected. Please Connect with Web3 (Metamask, trust Wallet)</h4>

                    <button onClick={findRandomNumber} className="btn btn-hover color-1 btn-glow px-5 my-4">Connect</button>

                </div>
                :
                mainLoading
                    ?
                    <div class="spinner-border text-light my-5" role="status">
                    </div>
                    :
                    <div className="my-5 ">
                        <div className="text-center">
                            <div class="row justify-content-center">
                                {currentRound === 1
                                    ?
                                    <div className="">
                                        <h5>This is First Round</h5>
                                        Find Random Number in between {firstRoundMin} - {firstRoundMax}
                                    </div>
                                    :
                                    <>
                                        <div class="col-4">
                                            <input type="number" className="py-3" ref={lowestNum} class="form-control form-control-lg" placeholder="lowest number" />
                                        </div>
                                        <div class="col-4">
                                            <input type="number" ref={highestNum} class="form-control form-control-lg" placeholder="highest Number" />
                                        </div>
                                    </>
                                }
                                <div className="col-12 mt-3">
                                    <button onClick={findRandomNumber} disabled={!isAdmin} className="btn btn-hover color-9 btn-glow px-5">Find Random Variable</button>
                                </div>
                                {findingRandomNumberNotice &&
                                    <p>{findingRandomNumberNotice}</p>
                                }

                                {loadingRandomNumber
                                    ?

                                    <div class="spinner-border text-light" role="status">
                                    </div>
                                    :
                                    loadedRandomNumber &&
                                    <div className="">
                                        <h1 className="glow-div p-3" >Random Number : <span className="text-warning">{loadedRandomNumber.randomNum}</span></h1>
                                        <h4>Your range {loadedRandomNumber.lowestNum}-{loadedRandomNumber.highestNum} | Date: {timeConverter(loadedRandomNumber.date)}</h4>
                                    </div>
                                }



                            </div>

                        </div>
                    </div>
            }

        </div >
    )
}



